.product{
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-item-align: stretch;
        align-self: stretch;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 10px 20px;
    margin: 10px;
    background-color: white;
    cursor: pointer;
    z-index: 1;
}

.product > img{
    height: 200px;
    -o-object-fit: contain;
       object-fit: contain;
}

.product > *:not(:last-child){
    -ms-flex-item-align: center;
        align-self: center;
    margin-bottom: 16px;
}

.product > h2{
    color: black;
    font-family: 'Roboto', sans-serif;
}

.product > h3{
    color: orange;
    font-family: 'Roboto', sans-serif;
}

.product > h3 > span{
    color: red;
    font-size: 10px;
    text-decoration: line-through;
}

.product > h4{
    font-family: 'Roboto', sans-serif;
    color: gold;
}


