.subtotal{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
    margin: 20px;
    padding: 10px;
    background-color: rgb(192, 189, 189);
}

.subtotal > *{
    margin: 10px 0;
}

.subtotal > * > span{
    color: red;
}

.gift{
    font-size: 12px;
    color: rgb(116, 109, 109);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.gift > * {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.input{
    margin: 4px;
}


