.btn{
    outline: none;
    border-radius: 1px;
    border: 1px solid;
    border-color: transparent;
    -webkit-border-image:  -webkit-gradient(linear,left top, right top,from(#a88734) ,color-stop(#9c7e31), to(#846a29)) 3;
    -webkit-border-image:  -webkit-linear-gradient(left,#a88734 ,#9c7e31, #846a29) 3;
         -o-border-image:  -o-linear-gradient(left,#a88734 ,#9c7e31, #846a29) 3;
            border-image:  -webkit-gradient(linear,left top, right top,from(#a88734) ,color-stop(#9c7e31), to(#846a29)) 3;
            border-image:  linear-gradient(to right,#a88734 ,#9c7e31, #846a29) 3;
    background-color: #f0c14b;
    padding: 5px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    font-size: 16px;
    font-weight: 600;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    color: white;
    margin: 0 auto;
    -webkit-box-shadow: 1px 1px 1px 0 #ca991d,
                -1px -1px 1px 0 #f5b617;
            box-shadow: 1px 1px 1px 0 #ca991d,
                -1px -1px 1px 0 #f5b617;
                display: -ms-flexbox;
                display: flex;
                -ms-flex-align: center;
                    align-items: center;
                -ms-flex-pack: center;
                    justify-content: center;
}

.btn:hover,
.btn:active{
    background-color: #b9890f;
    
}

.btn:active{
    -webkit-box-shadow: 8px 8px 10px 0 #ca991d inset,
                -3px -3px 6px 0 #f5b617 inset;
            box-shadow: 8px 8px 10px 0 #ca991d inset,
                -3px -3px 6px 0 #f5b617 inset;
}

.btn:disabled{
    background-color: gray;
    -webkit-box-shadow: none;
            box-shadow: none;
    cursor: not-allowed;
    -webkit-border-image:  -webkit-gradient(linear,left top, right top,from(#3a3937) ,color-stop(#353431), to(#2b2a27)) 3;
    -webkit-border-image:  -webkit-linear-gradient(left,#3a3937 ,#353431, #2b2a27) 3;
         -o-border-image:  -o-linear-gradient(left,#3a3937 ,#353431, #2b2a27) 3;
            border-image:  -webkit-gradient(linear,left top, right top,from(#3a3937) ,color-stop(#353431), to(#2b2a27)) 3;
            border-image:  linear-gradient(to right,#3a3937 ,#353431, #2b2a27) 3;
}


