.page{
    background-color: white;
    margin: 0 auto;
    border: 1px solid grey;
    padding: 10px;
    max-width: 320px;
    -ms-flex: 1 1;
        flex: 1 1;
    border-radius: 10px;
}

.page > *{
    margin: 20px auto;
}
.page > p{
    padding: 10px;
    text-align: left;
    color: rgb(175, 172, 172);
}

.page > h4 {
    color: #ccc;
    position: relative;
}

.page > h4::after{
    content: "";
    display: block;
    width: 50px;
    position: absolute;
    top: 50%;
    left: 0;
    border-bottom: rgba(204, 204, 204, 0.534) 1px solid;
}

.page > h4::before{
    content: "";
    display: block;
    width: 50px;
    position: absolute;
    top: 50%;
    right: 0;
    border-bottom: rgba(204, 204, 204, 0.555) 1px solid;
}

.login{
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -ms-flex-direction: column;
        flex-direction: column;
    background-color: rgba(0,0,0,0.2);
    -ms-flex-align: center;
        align-items: center;
    width: 500px;
    height: 630px;
    max-height: -webkit-min-content;
    max-height: -moz-min-content;
    max-height: min-content;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translateY(-50%) translateX(-50%);
        -ms-transform: translateY(-50%) translateX(-50%);
            transform: translateY(-50%) translateX(-50%);
    padding: 10px;
    border-radius: 10px;
}

.logo{
    width: 200px;
} 

.form{
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-item-align: start;
        align-self: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
}

.form > *{
    width: 100%;
}

.error{
    color: red;
    text-shadow: 0px 0px 2px red;
    font-size: 10px;
    text-transform: capitalize;
}


